import React, { createContext, useEffect, useState } from "react";
import raw from '../ip.txt'
import { io } from "socket.io-client";

export const IpContext = createContext();

export function IPprovider({ children }) {
    const [IPADDRES, setIPADDRES] = useState();
    useEffect(() => {
        fetch(raw)
            .then((response) => response.text())
            .then((text) =>
                setIPADDRES(
                    text
                    // String(text)
                    // .toLowerCase()
                    // .match(
                    //     '\b((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\.|$)){4}\b'

                    // )
                )
            );
    }, []);

    const ip = IPADDRES;
    const socket = io(ip);

    return (
        <IpContext.Provider value={[ip, socket]}>{children}</IpContext.Provider>
    );
}
